import i18n from 'i18next';
import React from 'react';
import ReactDom from 'react-dom';
import LanguageDetector from 'i18next-browser-languagedetector';
import { initReactI18next, Trans } from 'react-i18next';
import { EN, VN } from '_languages';
i18n.use(LanguageDetector)
    .use(initReactI18next)
    .init({
        resources: {
            en: {
                translations: EN,
            },
            vn: {
                translations: VN,
            },
        },
        fallbackLng: 'en',
        ns: ['translations'],
        defaultNS: 'translations',
        keySeparator: false,
        interpolation: {
            escapeValue: false,
        },
        react: {
            wait: true,
        },
        detection: {
            order: ['querystring', 'cookie', 'localStorage', 'htmlTag', 'path', 'subdomain'],
            lookupLocalStorage: 'app-language',
        },
    });

export default i18n;

export const t = (text: String) => {
    return <Trans>{text}</Trans>;
};
export const ts = (text: String) => {
    let _elm = document.createElement('span');
    ReactDom.render(<Trans>{text}</Trans>, _elm);
    return _elm.innerText;
};
