import dayjs from 'dayjs';
import { loadTheme } from 'office-ui-fabric-react/lib/Styling';
import { darkTheme, defaultTheme } from './themes';
import appConfig from 'appConfig';
const HM_THEME = 'HM_THEME';

const isPrimitiveTypes = (value: String) => {
    const primitiveTypes = ['boolean', 'number', 'string'];
    return primitiveTypes.indexOf(typeof value) > -1;
};

const formatDate = (date: string, displayTime: boolean) => {
    return date ? dayjs(date).format(displayTime ? 'DD/MM/YYYY HH:mm:ss' : 'DD/MM/YYYY') : '';
};
const tryGetData = (obj: any, key: String, defaultValue = null) => {
    try {
        if (isPrimitiveTypes(obj)) {
            return obj;
        }
        let d = obj;
        let arr = key.split('.');
        arr.forEach((e) => {
            d = d[e];
        });
        return d;
    } catch (error) {
        return defaultValue;
    }
};

export const loadAppTheme = () => {
    let d = localStorage.getItem(HM_THEME);
    if (!d) {
        return appConfig.darkmode;
    }
    return d === 'dark';
};
export const setAppTheme = (isDarkTheme: Boolean) => {
    let $body = document.getElementsByTagName('body')[0];
    if (isDarkTheme) {
        $body.classList.add('dark-mode');
        loadTheme({
            palette: darkTheme,
        });
        localStorage.setItem(HM_THEME, 'dark');
    } else {
        $body.classList.remove('dark-mode');
        loadTheme({
            palette: defaultTheme,
        });
        localStorage.setItem(HM_THEME, 'default-theme');
    }
};
export default {
    formatDate,
    tryGetData,
};
