import React from 'react';
import { Spinner, SpinnerSize } from 'office-ui-fabric-react/lib/Spinner';

export default () => {
    return (
        <div className="app-loading">
            <Spinner size={SpinnerSize.large} styles={{ circle: { zIndex: 4 } }} />
            <div className="background"></div>
        </div>
    );
};
