import React, { Component } from 'react';
import { Router, Route, Switch } from 'react-router-dom';
import { Provider } from 'react-redux';
import Loading from 'components/loading';
import history from 'helpers/history';
import { withTranslation, WithTranslation } from 'react-i18next';

require('helpers/i18n');

const DefaultLayout = React.lazy(() => import('./layout'));
// const LoginPage = React.lazy(() => import('./views/Pages/Login'));
// const ErrorPage = React.lazy(() => import('./views/Pages/Errors'));

export interface IAppProps extends WithTranslation {
    t: any;
    store: any;
}

class App extends React.Component<IAppProps> {
    public render() {
        let { t } = this.props;
        return (
            <Provider store={this.props.store}>
                <Router history={history}>
                    <React.Suspense fallback={<Loading />}>
                        <Switch>
                            {/* <Route exact path="/login" name={t('Login Page')} render={(props: any) => <LoginPage {...props} />} />
                            <Route exact path="/error" name={t('Error Page')} render={(props: any) => <ErrorPage {...props} />} /> */}
                            <Route path="/" name={t('Home')} render={(props: any) => <DefaultLayout {...props} />} />
                        </Switch>
                    </React.Suspense>
                </Router>
            </Provider>
        );
    }
}

export default withTranslation()(App);
