import { Reducer } from 'redux';
export const USER_CHANGE_INFO = 'USER_CHANGE_INFO';

export interface IUserState {
    username: string;
    fullname: string;
    role: string;
}
export interface IUserAction {
    type: string;
    payload: any;
}

export const user_change_info = (userInfo: any) => {
    return {
        type: USER_CHANGE_INFO,
        payload: userInfo,
    };
};
const initState: IUserState | null = null;
const userReducer: Reducer<IUserState | null, IUserAction> = (state = initState, action) => {
    let { type, payload } = action;
    switch (type) {
        case USER_CHANGE_INFO:
            return {
                ...state,
                ...payload,
            };
        default:
            return state;
    }
};

export default userReducer;
