import { Reducer } from 'redux';

const REGISTER_HOTKEY = 'REGISTER_HOTKEY';
const UNREGISTER_HOTKEY = 'UNREGISTER_HOTKEY';

export interface IHotKeyState {
    [key: string]: (keyEvent?: KeyboardEvent | undefined) => void;
}
export interface IHotKeyAction {
    type: string;
    payload: any;
}

export const registerHotkey = (handlers: { [key: string]: Function }) => {
    return {
        type: REGISTER_HOTKEY,
        payload: handlers,
    };
};
export const unRegisterHotkey = (keys: Array<string>) => {
    return {
        type: UNREGISTER_HOTKEY,
        payload: keys,
    };
};
const initState: IHotKeyState = {};

const hotkeyReducer: Reducer<IHotKeyState, IHotKeyAction> = (state = initState, action) => {
    let { type, payload } = action;
    switch (type) {
        case REGISTER_HOTKEY:
            return {
                ...state,
                ...payload,
            };
        case UNREGISTER_HOTKEY:
            let newState: { [key: string]: any } = { ...state };
            (<Array<string>>payload).forEach((k: string) => {
                delete newState[k];
            });
            return newState;
        default:
            return state;
    }
};

export default hotkeyReducer;
